export default {
	data() {
		return {
			titleList: [
				{content: '综合排序'},
				{content: '销量优先'},
				{content: '价格'},
			],
			titleBoxIndex: 0,
			yticonIconShangIndex: 0,
			goodsList: [],
			msg: '没有更多数据了',
			loadingTitle: '加载中'
		}
	},
	mounted () {
		this.getGoodsList();
	},
	methods: {
		titleBoxClick (index) {
			this.titleBoxIndex = index;
			if (this.titleBoxIndex == 2) {
				this.yticonIconShangIndex = 1;
				uni.showLoading({
					title: this.loadingTitle
				});
				this.goodsList.sort(this.compare2("price"));
				uni.hideLoading();
			} else if (this.titleBoxIndex == 1) {
				uni.showLoading({
					title: this.loadingTitle
				});
				this.goodsList.sort(this.compare2("sales"));
				uni.hideLoading();
				this.yticonIconShangIndex = 0;
			} else {
				this.yticonIconShangIndex = 0;
			}
		},
		yticonIconShang(val) {
			if (this.titleBoxIndex == 2) {
				this.yticonIconShangIndex = val;
				if (val == 1) {
					uni.showLoading({
						title: this.loadingTitle
					});
					this.goodsList.sort(this.compare2("price"));
					uni.hideLoading();
				} else {
					uni.showLoading({
						title: this.loadingTitle
					});
					this.goodsList.sort(this.compare("price"));
					uni.hideLoading();
				}
			} 
		},
		// 获取商品列表数据
		getGoodsList () {
			uni.showLoading({title: this.loadingTitle});
			this.$api.json('goodsList').then( res => {
				if (res) {
					uni.hideLoading();
					this.goodsList = res;
				};
			});
		},
		//这是比较函数
		compare(p){ 
		    return (m,n) => {
		        var a = m[p];
		        var b = n[p];
		        return a - b; //升序
		    }
		},
		//这是比较函数
		compare2(p){ 
		    return (m,n) => {
		        var a = m[p];
		        var b = n[p];
		        return b - a; //降序
		    }
		}
	}
}
